/* Base styles for larger screens */


/* POP UP */
.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modalContent {
  background-color: #ecd9c4;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
  width: 20%;
}

/* .order-form-bayar button {
  background-color: red;
} */

.qris-text {
  margin-left: 5px;
  color: white;
  /* background-color: red; */
}

.qris-check {
  color: red;
  background-color: red;
  justify-content: center;
  align-items: center;
  align-self: center;
  /* background-color: red;  */
  display: flex;
  height: 100%;
  padding: 0;
}

.is-qris {
  width: 30px;
  height: 30px;
  background-color: #ece0d1;
  
  border-radius: 8px;
  /* background-color: red; */
