}

/* In your OrderForm.css or a new print-specific stylesheet */

.order-form {
  display: flex;
  margin: 1%;
  flex-direction: row;
  min-height: 100vh;
}

.menu-container {
  flex: 2.3;
  margin-right: 20px;
  display: flex;
  flex-direction: column;
}

.nav-bar {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 80%;
  align-self: center;
  /* margin-bottom: 20px; */
}

.nav-button {
  margin-right: 0px;
  margin-left: 0px;
  margin-bottom: 10px;
  padding: 20px;
  padding-left: 15px;
  padding-right: 15px;
  /* width: px; */
  cursor: pointer;
  border: none;
  background-color: #4e3928;
  color: #ece0d1;
  border-radius: 8px;
  font-weight: 600;
  text-wrap: wrap;
  text-align: center;
  flex-direction: row;
}

.nav-button.active {
  background-color: #ece0d1;
  color: #4e3928;
  font-weight: 600;
}

.menu-section ul {
  margin-left: 0;
  padding-left: 0px;
}

.menu-section li {
  display: flex;
  justify-content: space-between;
}

.menu-header {
  margin-bottom: 5px;
  font-weight: 700;
  color: #4e3928;
}

li.menu-header {
  border-color: black;
  border-bottom-width: 2px;
  border-bottom-style: solid;
}

span.header-stock {
  flex: 1.5;
}

span.header-name {
  flex: 3;
}

span.header-price {
  flex: 2;
}

span.header-add {
  flex: 1;
  align-items: center;
  text-align: center;
}

.menu-item {
  margin-bottom: 10px;
  justify-content: space-between;
  display: flex;
}

.item-stock {
  flex: 1.5;
  display: flex;
  font-weight: 500;
  align-items: center;
  text-align: left;
  text-indent: 15px;
}

.out-of-stock {
  color: red;
  font-weight: 700;
}

span.item-name {
  display: flex;
  text-align: left;
  font-weight: 500;
  flex: 3;
  display: flex;
  align-items: center;
  justify-content: left;
}

.item-price {
  display: flex;
  text-align: left;
  align-items: center;
  flex: 2;
  font-weight: 500;
}

.add-icon {
  width: 20px;
  /* Adjust the size as needed */
  height: 20px;
  /* font-size: 16px; */
  filter: invert(20%) sepia(8%) saturate(2641%) hue-rotate(344deg) brightness(93%) contrast(85%);
}

.item-add {
  flex: 1;
  align-items: center;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

.order-form button {
  /* width: 60px;  */
  text-align: center;
  border: none;
}

.item-add-placeholder {
  flex: 1;
  align-items: center;
  background: none;
  border: none;
  cursor: default;
  padding: 0;
  margin: 5px;
}

.add-icon-placeholder {
  width: 20px;
  /* Adjust the size as needed */
  height: 20px;
  /* font-size: 16px; */
  filter: invert(20%) sepia(8%) saturate(2641%) hue-rotate(344deg) brightness(93%) contrast(85%);
  border: none;
  cursor: default;
}

/* CHEKOUT PESANAN */

/* PARENT */
.order-summary {
  flex: 2;
  display: flex;
  flex-direction: column;
  overflow-x: visible;
}

.order-cashier {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0;
}

#Your-Order {
  font-size: 22px;
}

#kasir {
  font-size: 22px;
  flex: 1;
  text-align: right;
  align-items: center;
}

.order-cashier input {
  background-color: #ecd9c4;
  border-color: #4e3928;
  border-radius: 8px;
  width: 120px;
  height: 100%;
  align-items: center;
  position: relative;
  font-size: 20px;
  padding-top: 5px;
  text-align: center;
  /* padding-right: 5px; */
  /* margin-top: 15px; */
  display: inline-block;
  white-space: wrap;
  /* Prevent text from wrapping */
  overflow: visible;
  /* Hide overflow content */
}

.order-summary ul {
  padding-left: 10px;
}

/* CHILD */
.order-item {
  margin-bottom: 5px;
  font-weight: 500;
}

/* Remove the spinners for WebKit browsers */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Remove the spinners for Firefox */
input[type="number"] {
  -moz-appearance: textfield;
  appearance: textfield;
  /* Standard property */
}

.order-name {
  flex: 3;
  font-weight: 500;
}

.order-price {
  flex: 3;
  align-items: right;
  text-align: left;
  text-indent: 20px;
}

.order-qty-container button {
  flex: 2;
  align-items: center;
  justify-content: center;
  /* height: 25px; */
  /* width: 25px; */
}

.order-item img {
  display: block;
  margin: 0;
  height: 30px;
  width: 30px;
  justify-content: center;
  align-items: center;
  filter: invert(20%) sepia(8%) saturate(2641%) hue-rotate(344deg) brightness(93%) contrast(85%);
}

.order-qty-container {
  display: flex;
}

.order-item {
  font-size: 18px;
}

button.order-qty-button {
  width: 30px;
  /* Adjust the size as needed */
  height: 30px;
  /* font-size: 16px; */
  justify-content: center;
  align-items: center;
  background: none;
  padding: 0px;
  cursor: pointer;
}

.order-qty-button:disabled img {
  cursor: not-allowed;
  filter: invert(16%) sepia(99%) saturate(7470%) hue-rotate(4deg) brightness(100%) contrast(114%);
}

input.order-qty-input {
  flex: 1;
  margin-left: 5px;
  margin-right: 5px;
  border: none;
  background: transparent;
  color: #4e3928;
  font-weight: 700;
  font-size: 18px;
  text-align: center;
  align-items: center;

  align-content: center;
  width: 20px;
  -webkit-appearance: none;
  -moz-appearance: textfield;
  appearance: textfield;
  /* Standard property */
}

.order-qty-input.max {
  border: 2px solid black;
  background-color: #ffcccc;
  font-weight: bold;
  color: red;
}

.order-remove img {
  height: 30px;
  width: 30px;
  filter: invert(16%) sepia(99%) saturate(7470%) hue-rotate(4deg) brightness(100%) contrast(114%);
  cursor: pointer;
}

button.order-remove {
  height: 30px;
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: none;
}

