/* Media Queries for Portrait Mode on Phones */

/* Mobile styles for screens with max width of 600px */
@media (max-width: 600px) {
  .order-form {
    flex-direction: column;
    padding: 10px;
  }

  .menu-container {
    flex: none;
    margin-right: 0;
  }

  .nav-bar {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }

  .nav-button {
    margin: 5px;
    padding: 8px;
    font-size: 14px;
  }

  .menu-section ul {
    padding-left: 0;
  }

  .menu-header {
    font-size: 12px;
  }

  .menu-item {
    flex-direction: row;
  }

  li.menu-item {
    margin-bottom: 10px;
  }

  .item-stock,
  .item-name,
  .item-price {
    font-size: 12px;
    text-align: left;
  }

  .out-of-stock {
    color: red;
  }

  .add-icon {
    width: 20px;
    /* Adjust the size as needed */
    height: 16px;
    font-size: 16px;
    filter: invert(20%) sepia(8%) saturate(2641%) hue-rotate(344deg) brightness(93%) contrast(85%);
  }

  .item-add {
    flex: 1;
    align-items: center;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
  }

  .item-add:disabled img {
    cursor: not-allowed;
    filter: invert(16%) sepia(99%) saturate(7470%) hue-rotate(4deg) brightness(100%) contrast(114%);
  }

  .item-add-placeholder {
    flex: 1;
    align-items: center;
    background: none;
    border: none;
    cursor: pointer;

    padding: 0;
    /* display: flex; Ensure flex container properties are applied */
    /* justify-content: center; Center the content inside */
  }

  /* PARENT */
  .order-summary {
    flex: none;
    margin-top: 20px;
  }

  .order-item {
    flex-direction: row;
    align-items: center;
    margin-bottom: 10px;
  }

  .order-name,
  .order-price {
    font-size: 12px;
  }

  .order-qty-container {
    flex-direction: row;
    align-items: center;
    display: flex;
  }

  input.order-qty-input {
    width: 8px;
    font-size: 16px;
    margin: 0;
    padding: 0;
  }

  .order-remove {
    /* width: 15px;
    height: 15px; */
    font-size: 10px;
    padding: 0;
    margin: 0;
  }

  .error-message {
    font-size: 14px;
  }

  .order-form-input {
    flex-direction: column;
    align-items: center;
  }

  .order-form-message .message-input {
    width: 100%;
    font-size: 14px;
    height: auto;
    margin-top: 5px;
  }

  .order-form-meja {
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
  }

  .order-form-meja input {
    width: 80%;
    font-size: 16px;
    margin-top: 5px;
  }

  .order-form-input button {
    font-size: 18px;
    padding: 10px;
  }

  .order-cashier input {
    width: 80%;
    font-size: 16px;
    margin-top: 5px;
  }
}